@charset "UTF-8";
.article-transfer__transfer-table {
  font-size: 1.125rem; }

@media (max-width: 767.98px) {
  .article-transfer__transfer-table tbody > tr > td {
    text-align: center; }
    .article-transfer__transfer-table tbody > tr > td:before {
      display: block;
      font-weight: bold; }
    .article-transfer__transfer-table tbody > tr > td:nth-child(1):before {
      content: "Город"; }
    .article-transfer__transfer-table tbody > tr > td:nth-child(2):before {
      content: "Легковой автомобиль"; }
    .article-transfer__transfer-table tbody > tr > td:nth-child(3):before {
      content: "Минивэн"; } }
